(function() {
	'use strict'
	angular
		.module('spmiFrontEnd')
		.controller('LoginController', LoginController)
		
	function LoginController ($scope, $state, $stateParams, $sanitize, $auth, CURRENT_USER, UserService) {
		
		var vm = this
		
		vm.input = {}
		vm.validated = false
		vm.errorLogin = false
		vm.alert = {}
	
		vm.load = function () {
			
		}

		vm.submit = function () {
	
			$scope.LoginForm.email.$setDirty();
			$scope.LoginForm.password.$setDirty();
			
			if ($scope.LoginForm.$valid) {
				vm.input.email = $sanitize(vm.input.email);
				vm.input.password = $sanitize(vm.input.password);
				$auth.login(vm.input).then(function(data){
					return UserService.identity()
				}, function(data) {
					alert(data);
				}).then(function(data){
					CURRENT_USER.id = data.id
					CURRENT_USER.name = data.name
					CURRENT_USER.type = data.type
					CURRENT_USER.status = data.status
					CURRENT_USER.nik = data.nik
					if(data.status == 2){
						if(data.type == 1){
							$state.go('main');
						}
						if(data.type == 2){
							$state.go('main.user');
						}
						
					}
					
				}, function(){})
	
			} else {
				vm.validated = true;
			}
		}
	
		vm.register = function() {
			$state.go('register')
		}
	
		vm.load();
		
		return vm
	}
})();
(function(){
	
	'use strict'
	
	angular
		.module('spmiFrontEnd')
		.factory('Authorization', Authorization)
		
	//Authorization.$inject =  ['$rootScope', '$state','$http', '$timeout', '$q', '$auth', 'UserService', 'API_HOST' ]
	function Authorization ($log, $rootScope, $state, $timeout, $q, $auth, UserService, CURRENT_USER) {
		
		function Authorization(){
			var self = this
			
			self.isAuthenticated = function (){
				
				var deferred = $q.defer()
				$timeout(function(){
					if($auth.isAuthenticated()){
						$log.info($auth.isAuthenticated())
						if (CURRENT_USER.id !== null && CURRENT_USER.id !== '' && CURRENT_USER.id !== undefined){
							$log.debug('Curent user exist: ' + CURRENT_USER.id)
							return CURRENT_USER
						} else {
							$log.warn('Curent user need to be fetched from database')
							return UserService.identity();
						}
					} else {
						$log.error('Current user not authenticated. login needed')
						deferred.reject()
						CURRENT_USER = {}
						$state.go('login');
					}
				},0).then(function(data){
					console.log(data)
					CURRENT_USER.id = data.id
					CURRENT_USER.name = data.name
					CURRENT_USER.type = data.type
					CURRENT_USER.status = data.status
					CURRENT_USER.nik = data.nik
					deferred.resolve()
				})
				
				return deferred.promise
			}
			
			self.authorize = function () {
				console.log('2 authorize')
				
				var deferred = $q.defer();
				UserService.identity()
					.then(function(response) {
						deferred.resolve();
						if ($rootScope.toState.data.type && $rootScope.toState.data.type.length > 0 &&
							!UserService.isInAnyRole($rootScope.toState.data.type)) {
		
							$state.go('main.denied')
						}
		
						if ($rootScope.toState.name == 'login') {
							$state.go($rootScope.fromState.name)
						}
		
					}, function(response) {
							deferred.resolve();
							$state.go('login')
					})
				
				return deferred.promise
			}
			
		}
		
		return new Authorization();
	}
})();
(function() {
    'use strict';

    angular
        .module('spmiFrontEnd')
        .run(AppRun);

        /** @ngInject */
    function AppRun($rootScope, $state, $stateParams, $timeout, API_HOST) {
        
        $rootScope.API_HOST = API_HOST;
        $rootScope.pushIfUnique = function (parent, child) {
            var i = 0;
            var counter = 0;
                
            for (i = 0 ; i < parent.length ; i++) {
                if (angular.equals(parent[i], child)) {
                    break;
                    return -1
                }
                counter++
            }
            if (counter === parent.length) {
                parent.push(child)
                return counter
            }
        }

	
        $rootScope.findObject = function (parent, child) {
            var i = 0;
            var counter = 0;

            for (i = 0 ; i < parent.length ; i++) {
                if (angular.equals(parent[i], child)) {
                    return i
                }
                counter++
            }

            if (counter === parent.length) {
                return -1
            }
        }

	
        $rootScope.first = false
        $rootScope.toDenied = false;

        $rootScope.run = false
	
        $rootScope.$on('$stateChangeStart', function(event, toState, toStateParams, fromState) {
		// track the state the user wants to go to; authorization service needs this
            
            $rootScope.toState = toState;
            $rootScope.toStateParams = toStateParams;
            
            $rootScope.fromState = fromState;
            
            //console.log(fromState.url + '->' + toState.url);

        });
    }

})();

(function() {
  'use strict';

  angular
    .module('spmiFrontEnd')
    .config(['$stateProvider', '$urlRouterProvider', RouterConfig]);

  /** @ngInject */
  function RouterConfig($stateProvider, $urlRouterProvider) {
	  
	$urlRouterProvider.otherwise('/');
	var promise = function ($rootScope, $q, $timeout, Authorization) {
		
		console.log('1');
		
		var deferred = $q.defer();
		Authorization.authorize().then(function(response) {
			deferred.resolve(response)
		}, function(response) {
			deferred.reject(response)
		})
		return deferred.promise;
	}

	var resolve = {
		Session: promise
	}
	
	$stateProvider
		.state('main', {
			url: '/',
			templateUrl: 'app/main/main.html',
			controller: 'MainController as vm',
			resolve: {
				isAuthenticated: function(Authorization){
					return Authorization.isAuthenticated();
				}
			}
      	})
	
	$stateProvider
		.state('register', {
			url:'/register',
			views: {
				'': {
					templateUrl: 'app/register/register.html',
					controller: 'RegisterController as vm'
				}
			},
			data: {
				type: []
			},
			resolve: {
				back: function($rootScope, $q, $state, UserService) {
					var deferred = $q.defer()

					UserService.identity()
						.then(function() {
							if($rootScope.toState.name == 'login' || $rootScope.toState.name == 'register') {
								deferred.resolve()
								$state.go($rootScope.fromState.name)
							}
							deferred.resolve()
						}, function() {
							deferred.resolve()
						})

					return deferred.promise
				}
			}

		})
		
		.state('error', {
			views: {
				'': {
					templateUrl: 'app/views/error.html'
				}
			},
			data: {
				type: []
			},
			resolve: {
				back: function($rootScope, $q, $state, UserService) {
					var deferred = $q.defer()

					UserService.identity()
						.then(function() {
							if($rootScope.toState.name == 'error') {
								deferred.resolve()
								$state.go($rootScope.fromState.name)
							}
							deferred.resolve()
						}, function() {
							deferred.resolve()
						})

					return deferred.promise
				}
			}
		})

		.state('register.information', {
			url: '/information',
			params: { 
		        // here we define default value for foo
		        // we also set squash to false, to force injecting
		        // even the default value into url
		        email: {
		          
		        },
		        // this param is not part of url
		        // it could be passed with $state.go or ui-sref 
		        hiddenParam: 'YES',
	      	},
			views: {
				'@': {
					templateUrl: 'app/views/information.html',
					controller: 'InformationRegistrationController'
				}
			}
		})

	

		

		



		

		.state('main.denied', {
			url: '/denies',
			params: { 
		        // here we define default value for foo
		        // we also set squash to false, to force injecting
		        // even the default value into url
		        email: {
		          
		        },
		        // this param is not part of url
		        // it could be passed with $state.go or ui-sref 
		        hiddenParam: 'YES',
	      	},
			views: {
				'@': {
					templateUrl: 'app/views/denied.html',
					controller: function ($scope, $state, $stateParams, UserService) {
						$scope.email = $stateParams.email

						$scope.logout = function() {
							UserService
								.logout()
								.then(function () {
									$state.go('login');
								})
						}
					}
				}
			},
			data: {
				type: []
			}
		})

		.state('main.notFound', {
			url: '/404',
			views: {
				'@': {
					template: '<h1>404 - Not Found</h1>'
				}
			}, 
			resolve: {
				Session: function() {
					return undefined;
				}
			}
		})
}

})();

